.custom-icon-svg {
  width: 1.5rem;
  height: 1.5rem;
  fill: currentColor;
  transition: fill 0.2s ease;

  :host-context(.p-menuitem-link-active) & {
    fill: var(--primary-color);
  }
}

// Small Caps text styling
.small-caps-text {
  font-weight: var(--navigation-font-weight);
  letter-spacing: 0.05em;
}

// Hover state styling
.menu-item-link {
  position: relative;
  transition: background-color 0.2s ease;

  &:hover {
    background-color: rgba(var(--color-primary-rgb, 66, 133, 244), 0.1);
  }
}
